.Content {
   width: 100%;
   height: 100%;
   overflow: hidden;
}

.upImg {
    display: flex;
    justify-content: center;
}
.upImg img{
    width: 100%;
    margin-left: 200px;
}
.nike270 {
    font-family: TradeGothic LT CondEighteen;
    font-size: 42px;
    text-transform: uppercase;
    color: #131212;
    margin: 0px 140px 0px;
}

.theNike {
    width: 370px;
    height: 60px;
    font-family: Helvetica Neue;
    font-size: 20px;
    color: #131212;
    opacity: 0.5;
    margin: 0px 140px 0px;
}

.shoes {
    font-family: TradeGothic LT CondEighteen;
    font-size: 20px;
    text-transform: uppercase;
    color: #131212;
    margin: -420px 140px 0px;
}

@media all and (max-width: 400px) {
    .Content img {
        width: 20%;
    }
}

@media all and (max-width: 600px) {
    .Content img {
        width: 40%;
    }
}

@media all and (max-width: 800px) {
    .Content img {
        width: 70%;
    }
}

@media all and (max-width: 1000px) {
    .Content img {
        width: 50%;
    }
}